import { configure } from '@storybook/react';
import { DocsPage, DocsContainer } from '@storybook/addon-docs/blocks';

// Import your components and any other dependencies required for your custom content

const withCustomContent = (Story, context) => {
  const storyWithCustomContent = (
    <>
      <h1>Custom Documentation</h1>
      <p>This is custom content for the component.</p>
      <Story {...context} />
    </>
  );

  return storyWithCustomContent;
};

export const parameters = {
  docs: {
    container: DocsContainer,
    page: DocsPage,
  },
};

export const decorators = [withCustomContent];

// Configure Storybook
configure(() => {}, module);